<template>
  <div class="client-info frame-container box align-default">
    <!-- 补白 -->
    <!-- 导航栏 -->
    <div class="client-info__nav box zsx-nav">
      <div class="title">进件</div>
      <!-- <div class="search" @click="onSearch">
        <mu-button class="zsx-mu-button" small color="info">
         <span class="add_css">+</span>
         添加商户
        </mu-button>
      </div> -->
    </div>
    <!-- 选项卡 -->
    <div class="client-info__tabs box align-bottom">
      <div
        v-for="(item, index) in tabList"
        :key="index"
        :class="['item', 'box', 'match-left-space', 'align-ver-bottom', index == current ? 'active' : '' ]"
        @click="onTabItemClick(index, item.status)">
        <div>{{item.name}}</div>
        <div class="bar mt-20"></div>
      </div>
    </div>
    <!-- 数据列表 -->
    <div class="client-info__content match-left-space mt-20">
      <mu-paper
        class="match-parent box pl-30 pr-20"
        :z-depth="1">
        <mu-container
          ref="container"
          v-loading="isLoading">
          <mu-load-more
            v-if="dataList && dataList.length"
            :refreshing="refreshing"
            @refresh="refresh"
            :loading="loading"
            :loaded-all="isLoadedAll"
            :loading-text="loadingText"
            @load="load">
            <mu-list style="padding:0;">
              <div
                v-for="(item, index) in dataList"
                :key="index"
                :class="['client-info__item box align-hor-bet', (index == dataList.length - 1) ? '' : '']"
                @click="onDataListItemClick(item)">
                <div class="align-ver-left">
                  <div class="title">
                    {{item.merchantName}}
                  </div>
                  <div class="subtile mt-6">
                    {{item.phone}}
                  </div>
                </div>
                <div class="match-left-space box align-left">
                  <div :class="['btn', 'match-left-space', 'align-right', (item.status == 3) ? 'active' : '']">
                    {{item.status | statusFilter}}
                  </div>
                  <div class="icon iconfont iconenter ml-20"></div>
                </div>
              </div>

            </mu-list>
          </mu-load-more>
          <div class="match-width align-center" v-else>
            <FzyNoData></FzyNoData>
            <!--
            <div class="no-data">
              <img
                src="../../assets/images/common/no-data.png"/>
              <div>暂无数据</div>
            </div>
            -->
          </div>
        </mu-container>
      </mu-paper>
    </div>

    <!-- 补脚 -->
    <div class="client-info__footer"></div>

  </div>
</template>

<script>
import { afterLoginInfoLocal, clientInfoDetailIDLocal } from '@/storage'
import { clientInfoApi } from '@/api'
import { CLIENT_INFO_DETAIL, CLIENT_INFO_ADD } from '@/router/types'
import FzyNoData from '@/components/common/fzyNoData'
export default {
  components: { FzyNoData },
  data() {
    return {
      tabList: [
        {
          name: '全部',
          status: ''
        },
        {
          name: '已提交',
          status: 2
        },
        {
          name: '已审核',
          status: 3
        },
        {
          name: '已激活',
          status: 5
        }
      ],
      current: 0,
      dataList: [],
      refreshing: false,
      loading: false,
      isLoadedAll: false,
      loadingText: '加载中',
      lodingDone: '-- END --',
      lodingMore: '下拉加载更多',
      pageNumber: 1,
      pageSize: 10,
      isLoading: false,
      status: '' // 签约状态
    }
  },

  methods: {
    // 选项卡切换
    onTabItemClick (index, status) {
      this.status = status
      this.current = +index
      this.pageNumber = 1
      this.dataList = []
      // 获取商户数据
      this.getMerchantList()
    },
    // 下拉刷新
    refresh () {
      this.refreshing = true
      this.$refs.container.scrollTop = 0
      setTimeout(() => {
        this.refreshing = false
      }, 2000)
      this.pageNumber = 1
      this.dataList = []
      // 获取商户数据
      this.getMerchantList()
    },
    // 加载更多
    load () {
      console.log('加载更多')
      this.loading = true
      setTimeout(() => {
        this.loading = false
        this.isLoadedAll = true
      }, 2000)
      this.pageNumber++
      // 获取商户数据
      this.getMerchantList()
    },
    // 获取商户数据
    getMerchantList () {
      let loginInfo = afterLoginInfoLocal.get()
      if (loginInfo) {
        loginInfo = JSON.parse(loginInfo)
        if (loginInfo && loginInfo.companyId) {
          clientInfoApi.getMerchantList(this.pageNumber, this.pageSize, this.status).then(res => {
            console.log(res)
            if (res.code === 200) {
              this.dataList = [
                ...this.dataList,
                ...res.obj.content
              ]
            } else {
              if (res && res.msg) {
                this.$toast.error(res.msg)
              }
            }
          }, err => {
            if (err && err.msg) {
              this.$toast.error(err.msg)
            }
          })
        } else {
          this.$toast.error('公司信息有误')
        }
      } else {
        this.$toast.error('登录失败')
      }
    },
    // 搜索
    onSearch () {
      // TODO
      console.log('search')
      this.$router.push({
          name: CLIENT_INFO_ADD,
          query: {}
        })
    },
    // 数据项
    onDataListItemClick (data) {
      // console.log(id)
      if (data.id) {
        let id = data.id
        clientInfoDetailIDLocal.set(JSON.stringify({id}))
        this.$router.push({
          name: CLIENT_INFO_DETAIL,
          query: {
            isDirect: data.isDirect,
            id:data.id
          }
        })
      } else {
        this.$toast.error('商户信息有误')
      }
    }
  },

  filters: {
    statusFilter (status) {
      let obj = {
        0: '未签约',
        1: '未开通',
        2: '待审核',
        3: '签约成功',
        4: '签约失败',
        5: '激活设备',
        null: '未签约'
      }
      return obj[status]
    }
  },

  mounted () {
    // TODO
    this.getMerchantList()
  }
}
</script>



